import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Icon from "@material-ui/core/Icon";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  card: {
    maxWidth: 345,
    marginBottom: 40,
    paddingBottom: 20
  },
  button: {
    marginRight: 10,
    fontSize: 10
  },
  price: {
    margin: "10px 0"
  }
});

export default function Special() {
  const [special, setSpecial] = useState([]);
  useEffect(() => {
    fetchSpecial();
  }, []);

  const classes = useStyles();

  //var backendHost = "https://casson-gallery.herokuapp.com";
  //var backendHost = "http://localhost:5000";
  var backendHost = "http://dev.casson.uk";

  function fetchSpecial() {
    fetch(`${backendHost}/Products`, {
      headers: {
        Accept: "application/json"
      }
    })
      .then(res => res.json())
      .then(json => {
        var specials = json.products.filter(product => product.special);
        setSpecial(specials);
      });
  }
  return (
    <div>
      <h3 className="category-heading">Special</h3>
      {special.map(special => (
        <Card className={classes.card}>
          <CardActionArea>
            <Link to={`/gallery/${special._id}`}>
              <CardMedia
                component="img"
                alt="Contemplative Reptile"
                height="auto"
                image={special.image}
                title="Contemplative Reptile"
              />
            </Link>
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                {special.name}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {special.description.split(".")[0]}
              </Typography>
              <Typography
                className={classes.price}
                gutterBottom
                variant="h5"
                component="h2"
                color="primary"
              >
                ${special.price}
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button variant="contained" size="small" color="secondary">
              <span className={classes.button}>Add to Cart </span>
              <Icon className="fa fa-shopping-cart" color="primary" />
            </Button>
            <Button size="small" color="primary" className={classes.button}>
              Learn More
            </Button>
          </CardActions>
        </Card>
      ))}
    </div>
  );
}
