import React, { useState, useEffect } from "react";
import Icon from "@material-ui/core/Icon";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  wholebutton: {
    marginTop: 25
  },
  button: {
    marginRight: 10
  }
});

export default function BestSeller() {
  const [sellers, setSellers] = useState([]);

  useEffect(() => {
    fetchSellers();
  }, []);
  //var backendHost = "https://casson-gallery.herokuapp.com";
  //var backendHost = "http://localhost:5000";
  var backendHost = "http://dev.casson.uk";
  const classes = useStyles();

  function fetchSellers() {
    fetch(`${backendHost}/Products`, {
      headers: {
        Accept: "application/json"
      }
    })
      .then(res => res.json())
      .then(json => {
        var sellers = json.products.filter(product => product.bestSeller);
        setSellers(sellers);
      });
  }
  return (
    <div>
      <h3 className="category-heading">Best Sellers</h3>
      <div id="sellers">
        {sellers.map(seller => (
          <Paper>
            <div
              class="card seller-card"
              style={{ padding: "5em 2em 2em 2em" }}
            >
              <Link to={`/gallery/${seller._id}`}>
                <img src={seller.image} class="card-img-top" alt="..." />
              </Link>
              <div class="card-body">
                <h4 class="card-title">{seller.name}</h4>
                <a href="#" class="card-link price">
                  ${seller.price}
                </a>
                <br />
                <Button
                  variant="contained"
                  color="secondary"
                  className={classes.wholebutton}
                >
                  <span className={classes.button}>Add to Cart </span>
                  <Icon className="fa fa-shopping-cart" color="primary" />
                </Button>
                <a href="#" class="card-link like" style={{ float: "right" }}>
                  <i class="fa fa-heart-o"></i>
                </a>
              </div>
            </div>
          </Paper>
        ))}
      </div>
    </div>
  );
}
