import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%"
  },
  heading: {
    fontSize: theme.typography.pxToRem(25),
    fontWeight: theme.typography.fontWeightRegular
  }
}));

export default function SimpleExpansionPanel() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}> Photo Gallery</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <ListItem button>
            <ListItemText primary="Casson Specials" />
          </ListItem>
          <Divider light />
          <ListItem button>
            <ListItemText primary="Ultimate Selfies" />
          </ListItem>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography className={classes.heading}>Art</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <ListItem button>
            <ListItemText primary="Painting" />
          </ListItem>
          <Divider light />
          <ListItem button>
            <ListItemText primary="Texture Design" />
          </ListItem>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography className={classes.heading}> Caribbean Art</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <ListItem button>
            <ListItemText primary="Explore Cribbean Art" />
          </ListItem>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
}
