import React, { useState, useEffect } from "react";
import Icon from "@material-ui/core/Icon";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  wholebutton: {
    marginTop: 25
  },
  button: {
    marginRight: 10
  }
});

export default function Featured() {
  const [featured, setFeatured] = useState([]);

  useEffect(() => {
    fetchFeatured();
  }, []);

  const classes = useStyles();
  //var backendHost = "https://casson-gallery.herokuapp.com";
  //var backendHost = "http://localhost:5000";
  var backendHost = "http://dev.casson.uk";

  function fetchFeatured() {
    fetch(`${backendHost}/Products`, {
      headers: {
        Accept: "application/json"
      }
    })
      .then(res => res.json())
      .then(json => {
        var featured = json.products.filter(product => product.featured);
        setFeatured(featured);
      });
  }
  return (
    <div>
      <h3 className="category-heading">Featured Products</h3>
      <div id="featured">
        {featured.map(feature => (
          <div class="card" style={{ padding: "5em 2em 2em 2em" }}>
            <Link to={`/gallery/${feature._id}`}>
              <img src={feature.image} class="card-img-top" alt="..." />
            </Link>
            <div class="card-body">
              <h4 class="card-title">{feature.name}</h4>
              <a href="#" class="card-link price">
                ${feature.price}
              </a>
              <br />
              <Button
                variant="contained"
                color="secondary"
                className={classes.wholebutton}
              >
                <span className={classes.button}>Add to Cart </span>
                <Icon className="fa fa-shopping-cart" color="primary" />
              </Button>
              <a href="#" class="card-link like" style={{ float: "right" }}>
                <i class="fa fa-heart-o"></i>
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
