import React from "react";
import { makeStyles, withTheme } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Icon from "@material-ui/core/Icon";
import Fab from "@material-ui/core/Fab";

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(0, 0, 10, 0),
    position: "relative"
  },
  /*middlePaper: {
    transform: "scaleX(1.05)",
    zIndex: "2"
  },*/
  top: {
    background: "#212121",
    color: "white",
    padding: 20,
    margin: "20px 0"
  },
  bottom: {
    padding: 20,
    margin: "0 20px",
    textAlign: "center"
  },
  button: {
    position: "absolute",
    top: 30,
    right: 12
  },
  promo: {
    display: "grid",
    gridGap: 1,
    gridTemplateColumns: "repeat(auto-fit, minmax(240px, 1fr))",
    margin: "5vw",
    position: "relative"
  }
}));

export default function Promo() {
  const classes = useStyles();

  return (
    <div className={classes.promo}>
      <Paper className={classes.root}>
        <Typography variant="h5" component="h3" className={classes.top}>
          Free Shopping
        </Typography>
        <Fab color="primary" aria-label="add" className={classes.button}>
          <Icon className="fa fa-shopping-cart" color="secondary" />
        </Fab>
        <Typography component="p" className={classes.bottom}>
          Free delivery on all orders over $199
        </Typography>
      </Paper>
      <Paper className={classes.root}>
        <Typography variant="h5" component="h3" className={classes.top}>
          Money Back Guarantee
        </Typography>
        <Fab color="primary" aria-label="add" className={classes.button}>
          <Icon className="fa fa-money" color="secondary" />
        </Fab>
        <Typography component="p" className={classes.bottom}>
          We offer 15 day easy return
        </Typography>
      </Paper>
      <Paper className={classes.root}>
        <Typography variant="h5" component="h3" className={classes.top}>
          Online Support 24/7
        </Typography>
        <Fab color="primary" aria-label="add" className={classes.button}>
          <Icon className="fa fa-phone" color="secondary" />
        </Fab>
        <Typography component="p" className={classes.bottom}>
          Our operators are available 24/7
        </Typography>
      </Paper>
    </div>
  );
}
